.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .logoText {
  display: flex;
  cursor: pointer;
  align-items: center;
}
.header .logoText .logo__text {
  margin-left: 6.5px;
  font-family: "Slackey", serif;
  font-size: 22px;
  font-weight: 400;
}
.header .date {
  font-family: "PoppinsSemiBold", serif;
  font-size: 24px;
  font-weight: 600;
  margin-left: clamp(1.25rem, -1.307vw + 1.569rem, 0rem);
}

